import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';
import ShareObjectsApp from './app';
import sharing from './reducers/sharing-store';

Registry.registerComponent(prefixNS('ShareObjectsApp'), ShareObjectsApp);

Registry.registerReducers({
    sharing
});

export default {
    ShareObjectsApp: ShareObjectsApp
};



